import React, { useState, useEffect } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import styles from "../styles/Resume.module.css";

// Usar worker de CDN
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const Resume = () => {
  const [selectedLanguage, setSelectedLanguage] = useState("es");
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const handleLanguageChange = (language) => {
    setSelectedLanguage(language);
  };

  const handleDownload = () => {
    const cvFile = selectedLanguage === "es" ? "/cv-es.pdf" : "/cv-en.pdf";
    const link = document.createElement("a");
    link.href = cvFile;
    link.download = `CV_Marta_Cuevas_${selectedLanguage.toUpperCase()}.pdf`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  // Determinar escala según ancho de pantalla
  const scale = windowWidth > 786 ? 1.5 : 0.6;

  return (
    <div className={styles.resumePage}>
      <div className={styles.container}>
        <div className={styles.resumeHeader}>
          <h1 className={styles.pageTitle}>Resume</h1>

          <div className={styles.controls}>
            <div className={styles.languageSelector}>
              <button
                className={`${styles.languageBtn} ${
                  selectedLanguage === "es" ? styles.active : ""
                }`}
                onClick={() => handleLanguageChange("es")}
              >
                Español
              </button>
              <button
                className={`${styles.languageBtn} ${
                  selectedLanguage === "en" ? styles.active : ""
                }`}
                onClick={() => handleLanguageChange("en")}
              >
                English
              </button>
            </div>

            <button className={styles.downloadButton} onClick={handleDownload}>
              <i className="fas fa-download"></i>
              Download
            </button>
          </div>
        </div>

        <div className={styles.pdfViewer}>
          <Document
            file={selectedLanguage === "es" ? "/cv-es.pdf" : "/cv-en.pdf"}
            className={styles.pdfDocument}
            loading={
              <div style={{ color: "white", textAlign: "center", padding: "2rem" }}>
                Loading PDF...
              </div>
            }
            error={
              <div style={{ color: "white", textAlign: "center", padding: "2rem" }}>
                Error loading PDF
              </div>
            }
          >
            {/* Página 1 */}
            <Page pageNumber={1} scale={scale} renderMode="svg" className={styles.pdfPage} />
            {/* Página 2 */}
            <Page pageNumber={2} scale={scale} renderMode="svg" className={styles.pdfPage} />
            {/* Agrega más <Page> si tu PDF tiene más páginas */}
          </Document>
        </div>
      </div>
    </div>
  );
};

export default Resume;
